import React from 'react';
import Minter from "./Minter";
import mainLogo from './../img/hero-img.png';
function Hero() {

  return (

      <section id="hero" className="padd-section text-center wow fadeInUp">
        <div className="hero-container">
          <h1>MetaMaps</h1>
          <h2>Worlds for your NFTs to roam free</h2>
          <img src={mainLogo} alt="Hero Imgs" />
        
          <Minter />

        
          <br/>
          <div className="btns">
            <a href="https://opensea.io/collection/metamapsnft" target="_blank"> OpenSea</a>
            <a href="https://twitter.com/metamapsnft" target="_blank"> Twitter</a>
            <a href="https://discord.gg/Y2h5dPAhBb" target="_blank"> Discord</a>
          </div>
          <br/> <br/>
        </div>
      </section>
    );
  }
export default Hero;