import React, { useEffect, useState, useRef } from "react";
import { useParams } from "react-router-dom";
import axios from 'axios';

import { useDispatch, useSelector } from "react-redux";
import { connect } from "../redux/blockchain/blockchainActions";
import { fetchData } from "../redux/data/dataActions";

import mainLogo from './../img/hero-img.png';

import * as s from "../styles/globalStyles";
import styled from "styled-components";
//import { create } from "ipfs-http-client";

export const StyledButton = styled.button`
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 13px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 11px 36px;
  border-radius: 50px;
  transition: 0.5s;
  margin-bottom: 30px;
  border: 2px solid #71c55d;

  color: #71c55d;
  text-decoration: none;
`;


export default function Map() {
  const id2 = String(useParams()['id']);

  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  //const data = useSelector((state) => state.data);
  const [Outcome, setOutcome] = useState("");
  const [Travel, setTravel] = useState("");
  const [data,setData]=useState({
    dna:'',
    mountain:'',
    trees:'',
    grass:'',
    sand:'',
    gems:'',
  }
  );

  

  useEffect(() => {
    getData();
  }, [blockchain.account]);
  
  useEffect(() => {
    document.title = "MetaMaps";
  }, []);
  
  useEffect(()=>{
    axios.get("./maps/" + id2 + ".json")
        .then(res=>{
            //console.log(res)
            let attrData=res.data;
            

            setData({dna:attrData.dna, 
              mountain:attrData.attributes[0].value,
              trees:attrData.attributes[1].value,
              grass:attrData.attributes[2].value,
              sand:attrData.attributes[3].value,
              gems:attrData.attributes[4].value})
            setTravel("https://metamaps.netlify.app/" + res.data.dna)
            console.log(Travel)
        })
        .catch(err=>{
            console.log(err);
        })
  },[])





  const getData = () => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {
      dispatch(fetchData(blockchain.account));
    }
  };

  if (id2 && Number(id2) < 1515 ){
    const imgurl = "./maps/" + id2 + ".png";


    const checkNFTs = (_num) => {
    
    blockchain.smartContract.methods
      .ownerOf(_num)
      .call(function(err,res)
      {
        if(!err) {
          if(blockchain.account.toUpperCase() == res.toUpperCase()){
            setOutcome("Travel");  
            setTravel("https://metamaps.netlify.app/play/?id=" + data.dna)

          }else{
            setOutcome("Mint your MetaMap!");
            setTravel("https://metamaps.netlify.app/")
          } 
        }else{
          setOutcome("Sorry, something went wrong please try again later.");
        } 
      });
    };
      
      return(
      <>
        <section id="team" className="padd-section text-center wow fadeInUp">
          <div className="container">
            <div className="section-title text-center">
              <h2>MetaMap #{id2}</h2>
            </div>
          </div>
          <div className="container">
            <div className="row">
              <div className="col-md-6 col-md-4 col-lg-4">
                <div className="team-block bottom">
                  <img src={imgurl} className="img-responsive" alt="img" />
                  <div className="team-content">
                    <h6>Gems: {data.gems}</h6>
                  </div>
                </div>
              </div>
              <div className="col-md-6 col-md-4 col-lg-4">
                <div className="team-block bottom" text-align="left">
                  
                  <h3>
                  Terrain Data
                  </h3>
                  <table>
                    <thead>
                      <tr>
                        <th>Terrain Type</th>
                        <th>Altitude</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td>Mountain</td>
                        <td>{data.mountain}</td>
                      </tr>
                      <tr>
                        <td>Trees</td>
                        <td>{data.trees}</td>
                      </tr>
                      <tr>
                        <td>Grass</td>
                        <td>{data.grass}</td>
                      </tr>
                      <tr>
                        <td>Sand</td>
                        <td>{data.sand}</td>
                      </tr>
                      <tr>
                        <td>Water</td>
                        <td>0.00</td>
                      </tr>
                    </tbody>
                    </table>
                </div>
              </div>
              
              <div className="col-md-6 col-md-4 col-lg-4">
                <div className="team-block bottom" text-align="left">
                  <h4>
                  Enter the metaverse
                  </h4>
                
                  {blockchain.account === "" ||
                  blockchain.smartContract === null ? (
                    <s.Container ai={"center"} jc={"center"}>
                      <StyledButton className="btn-get-started scrollto"
                        onClick={(e) => {
                          e.preventDefault();
                          dispatch(connect());
                          
                          getData();
                        }}
                      >
                        Connect
                      </StyledButton>
                      {blockchain.errorMsg !== "" ? (
                        <>
                          <s.SpacerSmall />
                            {blockchain.errorMsg}
                        </>
                      ) : null}
                    </s.Container>
                  ) : (
                    <s.Container ai={"center"} jc={"center"} fd={"row"}>
                      <StyledButton className="btn-get-started scrollto"
                        
                        onClick={(e) => {
                          e.preventDefault();
                          getData();
                          checkNFTs(id2);
                          
                        }}
                      >
                        Check
                      </StyledButton>
                      <s.SpacerSmall />
                            <a href={Travel}>{Outcome}</a>
                            
                    </s.Container>
                    
                  )}
                  
                </div>
              </div> 
                
            </div>
          </div>
        </section>
      </>
    )
  }else{
    return(
      <>
        <section id="team" className="padd-section text-center wow fadeInUp">
          <div className="container">
            <div className="section-title text-center">
              <h2>Enter MetaMap #</h2>
       
              <s.SpacerSmall />
              <form>
                <label>
                  Map ID#:
                  <s.SpacerSmall />
                  <input type="text" name="id" onKeyPress={(e) => {if (e.key === 'Enter') {
                  this.context.history.push(e.target.value)}}}/>
       
 
                </label>
              </form>
            </div>
          </div>
          
        </section>     



      </>
    )

  }
  
  
}